import React, { Component } from 'react';
import { featureCollection } from '@turf/helpers';

let pointData = featureCollection([]);
export default class Map extends Component {
  constructor(props) {
    super(props);
    mapboxgl.accessToken = this.props.mapboxAccessToken;
    this.state = {
      points: []
    }
  }

    // I wonder if component will receive props is how we want to execute the flyTo
  // if new coords are received then execute the flyTo.

  componentDidMount() {
    // @NOTE: MAKE SURE ARRAY IS [LONGITUDE, LATITUDE]
    const bounds = [
      [-60.80409032, 0.3332811], //southwest
      [-52.41053563, 6.90258397] //northeast
    ]

    this.map = new mapboxgl.Map({
      container: this.mapContainer,
      style: this.props.mapboxStyle,
      center: [-55.63, 4.78],
      zoom: 7.6,
      maxBounds: bounds
    });
    this.map.addControl(new mapboxgl.NavigationControl());
    if(this.props.points.length) {
      pointData = featureCollection(this.props.points);
      this.setState({points: this.props.points});
      this.map.on('load', () => {
        this.map.addSource('pointData', {
          type: 'geojson',
          data: pointData
        })
        this.map.addLayer({
          id: 'story-points',
          source: 'pointData',
          type: 'circle',
          paint: {
            "circle-color": "pink",
            "circle-radius": 10
          }
        });
      });
    }
  }
  render() {
    return (
      <div ref={
        el => this.mapContainer = el
      }
      className = "ts-MainMap"/>
    );
  }
}

